@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  src: local(Inter), local(Inter-Regular), url("Inter-Regular.0203edb2.woff2") format("woff2"), url("Inter-Regular.57f65c94.woff") format("woff");
}

@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  src: local(Inter Medium), local(Inter-Medium), url("Inter-Medium.5b7cb347.woff2") format("woff2"), url("Inter-Medium.2fd7c5cc.woff") format("woff");
}

@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  src: local(Inter Bold), local(Inter-Bold), url("Inter-Bold.0822484b.woff2") format("woff2"), url("Inter-Bold.b36d5f49.woff") format("woff");
}

html {
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  word-break: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: #0000;
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
}

html, body, * {
  font-family: Inter, sans-serif;
}

body {
  color: var(--colors-zekrom);
  background: #fff;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
}

*, :after, :before {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.app__header {
  border-bottom: var(--border);
  background-color: var(--colors-bg-surface);
}

.header__container {
  height: var(--header-height);
  align-items: center;
  display: flex;
}

.app__footer {
  border-top: var(--border);
  background-color: var(--colors-bg-surface);
}

.footer__container {
  align-items: center;
  padding: 1rem 0;
  font-size: .875rem;
  display: flex;
}

.app-container {
  max-width: 80rem;
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.pr-hero {
  height: calc(100vh - var(--header-height));
  background-color: var(--colors-primary-l800);
  padding: 2rem 0;
}

.pr-hero .app-container {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.pr-hero__container {
  border: dashed 1px var(--colors-primary-l200);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: 1rem 0;
  display: flex;
}

.pr-hero__left {
  flex-direction: column;
  flex: 1;
  gap: 1.25rem;
  padding-left: 3rem;
  display: flex;
}

.pr-hero__right {
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-right: auto;
  display: flex;
}

.pr-hero__footer {
  flex: 100%;
  gap: 1rem;
  padding-left: 3rem;
  display: flex;
}

.pr-hero__header {
  color: var(--colors-zekrom);
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 3rem;
}

.pr-hero__sub-header {
  color: var(--colors-pancham);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.5rem;
}

.app__btn-main {
  cursor: pointer;
  user-select: none;
  color: #fff;
  background: var(--colors-primary);
  border: 1px solid var(--colors-primary);
  border-radius: 2px;
  outline-color: #0000;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.app__btn-lg {
  padding: .625rem .875rem;
  font-size: 1.125rem;
  font-weight: 500;
}

.app__btn-xlg {
  padding: .875rem 1rem;
  font-size: 1.25rem;
  font-weight: 500;
}

.app__gb {
  flex-direction: column;
  align-items: center;
  width: 52px;
  padding: 2px;
  display: flex;
  position: relative;
}

.app__gb:hover {
  cursor: pointer;
}

.app__gb:hover.app__gb--d2 .app__gb-img-cont {
  background-color: #ca3c1d;
}

.app__gb:hover.app__gb--cs .app__gb-img-cont {
  background-color: #c3943f;
}

.app__gb-status {
  background-color: var(--colors-pancham);
  opacity: .7;
  border-radius: 2px;
  padding: 2px;
  display: inline-flex;
  position: absolute;
  bottom: -4px;
  right: -4px;
}

.app__gb-status .lucide {
  width: 16px;
  height: 16px;
}

.app__gb-status--active {
  color: var(--colors-success400);
}

.app__gb-status--waiting {
  color: var(--colors-warning400);
}

.app__gb-img-cont {
  background-color: var(--colors-primary-l300);
  border-radius: 2px;
  width: 48px;
  height: 48px;
}
